@import url(https://fonts.googleapis.com/css?family=Ubuntu:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gochi+Hand|Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu:700&display=swap);
.header{display:flex;flex-direction:column;align-items:center}.header__logo{width:300px}

.preview{width:200px;height:270px;margin:10px;display:flex;flex-direction:column;align-items:center}.preview__photo{height:180px;width:180px;border-radius:50%;background-color:rgba(159,144,175,0.623);margin-bottom:10px}.preview__info{display:flex;align-items:center;flex-direction:column;background-color:rgba(159,144,175,0.623);border-radius:3px;width:180px;padding:2px}.preview__info--gem{position:absolute;-webkit-transform:translateX(50px) translateY(-60px);transform:translateX(50px) translateY(-60px);height:60px}.preview__info--name{font-family:'Ubuntu', sans-serif;text-align:center}

.main{display:flex;flex-wrap:wrap}

.details{margin:35px;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:20px;background-color:rgba(159,144,175,0.623)}.details__img{width:90%;-webkit-transform:translateY(-60px);transform:translateY(-60px)}.details__gem{display:flex;flex-direction:column;position:absolute;align-items:center;-webkit-transform:translateX(-120px) translateY(0px);transform:translateX(-120px) translateY(0px);border-radius:20px}.details__gem--img{width:80px}.details__gem--name{font-family:'Staatliches', cursive}.details__info{display:flex;flex-direction:column;justify-content:center;align-items:center}.details__info--name{font-family:'Ubuntu', sans-serif}

.filter{display:flex;flex-direction:column;align-items:center;width:300px;margin:0}.filter__label{font-family:'Ubuntu', sans-serif}.filter__input{border-radius:2px;font-size:15px;padding:2px;border-style:none;font-family:'Ubuntu', sans-serif}.filter__collapsible{cursor:pointer}

*{box-sizing:border-box;margin:0;padding:0}body{background:url("https://66.media.tumblr.com/a639bda231b0f3a803643073e4df0d63/tumblr_oy1ql1bPTh1u21nzho1_1280.png");background-size:cover;background-attachment:fixed;background-repeat:no-repeat;background-position:center}

